import React from "react";
import {
  List,
  Datagrid,
  TextField,
  DataField,
  EditButton,
  DeleteButton,
} from "react-admin";

const QuestionsList = () => {
  return (
    <List>
      <Datagrid>
        <TextField source="id" />
        <TextField source="title" label="" />
        <TextField source="video_url" label=" " />
        <TextField source="text" label=" " />
        <TextField source="position" label="" />
        <TextField source="answer_summery" label=" " />
        <EditButton basePath="/questions" />
        <DeleteButton basePath="/questions" />
      </Datagrid>
    </List>
  );
};

export default QuestionsList;
